<template>
  <div class="container-fluid p-4">
    <!-- 数据加载状态 -->
    <div v-if="stockStore.isLoading" class="alert alert-info text-center animate-pulse">
      <i class="bi bi-hourglass-split me-2"></i>正在加载股票数据...
    </div>

    <!-- 错误提示 -->
    <div v-if="stockStore.error && !stockStore.isLoading" class="alert alert-danger shadow-lg">
      <i class="bi bi-exclamation-triangle me-2"></i>{{ stockStore.error }}
      <button class="btn-close ms-3" type="button" aria-label="Close" @click="clearError"></button>
    </div>

    <!-- 无数据提示 -->
    <div v-if="!stockStore.isLoading && !stockStore.error && stockStore.totalCount === 0" class="alert alert-warning text-center">
      <i class="bi bi-database-slash me-2"></i>未加载到股票数据，请上传CSV文件
      <input type="file" class="form-control mt-3" accept=".csv" @change="handleFileUpload" />
    </div>

    <!-- 图表区域 -->
    <div v-if="!stockStore.isLoading && !stockStore.error && stockStore.totalCount > 0" class="chart-container">
      <!-- 数据概览统计卡片 -->
      <div class="row mb-4">
        <div class="col-md-3" v-for="(stat, index) in stats" :key="index">
          <div class="card" :class="stat.class + ' text-white shadow'">
            <div class="card-body">
              <h6 class="card-title">{{ stat.title }}</h6>
              <h2 :class="stat.size">{{ stat.value }}</h2>
            </div>
          </div>
        </div>
      </div>

      <!-- 股价趋势图 -->
      <div class="card mb-4 shadow">
        <div class="card-header d-flex flex-wrap justify-content-between align-items-center gap-2">
          <h5 class="card-title mb-0">股票价格趋势图</h5>
          <div class="d-flex gap-2">
            <select class="form-select w-auto" v-model="priceTimeRange" @change="updatePriceChart">
              <option value="1d">1天</option>
              <option value="1w">1周</option>
              <option value="1m">1月</option>
              <option value="3m">3月</option>
              <option value="1y">1年</option>
              <option value="all">全部</option>
            </select>
            <select class="form-select w-auto" v-model="selectedStocks" multiple @change="updatePriceChart" :disabled="!stockNames.length">
              <option v-for="name in stockNames" :key="name" :value="name">{{ name }}</option>
            </select>
          </div>
        </div>
        <div class="card-body position-relative">
          <div id="priceTrendChart" class="w-100" style="height: 400px;"></div>
          <div v-if="isPriceChartLoading" class="chart-loading">
            <i class="bi bi-spinner bi-spin me-2"></i>图表渲染中...
          </div>
          <!-- 无数据提示 -->
          <div v-if="!isPriceChartLoading && priceChartNoData" class="chart-loading">
            <i class="bi bi-info-circle me-2"></i>所选时间范围内没有数据
          </div>
        </div>
      </div>

      <!-- 成交量与涨跌幅分析 -->
      <div class="card shadow">
        <div class="card-header d-flex flex-wrap justify-content-between align-items-center gap-2">
          <h5 class="card-title mb-0">成交量与涨跌幅关联分析</h5>
          <div class="d-flex gap-2">
            <select class="form-select w-auto" v-model="volumeTimeRange" @change="updateVolumeChart">
              <option value="1d">1天</option>
              <option value="1w">1周</option>
              <option value="1m">1月</option>
              <option value="3m">3月</option>
              <option value="1y">1年</option>
              <option value="all">全部</option>
            </select>
            <select class="form-select w-auto" v-model="selectedStockForVolume" @change="updateVolumeChart" :disabled="!stockNames.length">
              <option v-for="name in stockNames" :key="name" :value="name">{{ name }}</option>
            </select>
          </div>
        </div>
        <div class="card-body position-relative">
          <div id="volumeChangeChart" class="w-100" style="height: 400px;"></div>
          <div v-if="isVolumeChartLoading" class="chart-loading">
            <i class="bi bi-spinner bi-spin me-2"></i>图表渲染中...
          </div>
          <!-- 无数据提示 -->
          <div v-if="!isVolumeChartLoading && volumeChartNoData" class="chart-loading">
            <i class="bi bi-info-circle me-2"></i>所选时间范围内没有数据
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, onMounted, nextTick, onUnmounted, computed } from 'vue'
import * as echarts from 'echarts'
import { useStockStore } from '@/stores/stockStore'

// 状态管理
const stockStore = useStockStore()
const stockNames = ref([])
const selectedStocks = ref([])
const selectedStockForVolume = ref('')
const priceTimeRange = ref('1m')
const volumeTimeRange = ref('1m')
const isPriceChartLoading = ref(false)
const isVolumeChartLoading = ref(false)
const priceChartNoData = ref(false)
const volumeChartNoData = ref(false)

// 图表实例
let priceChart = null
let volumeChart = null

// 统计卡片数据
const stats = computed(() => [
  { title: '股票总数', value: stockNames.value.length, class: 'bg-gradient-primary', size: 'display-4' },
  { title: '数据总条数', value: stockStore.totalCount.toLocaleString(), class: 'bg-gradient-success', size: 'display-4' },
  { title: '平均股价', value: calculateAveragePrice.value, class: 'bg-gradient-info', size: 'h5' },
  { title: '最大成交量', value: calculateMaxVolume.value, class: 'bg-gradient-warning', size: 'h5' }
])

// 计算平均股价
const calculateAveragePrice = computed(() => {
  if (!stockStore.totalCount) return '$0.00'
  
  let total = 0
  let count = 0
  
  Object.values(stockStore.processedData).forEach(data => {
    data.forEach(item => {
      total += parseFloat(item.last || 0)
      count++
    })
  })
  
  return `$${(total / count).toFixed(2)}`
})

// 计算最大成交量
const calculateMaxVolume = computed(() => {
  if (!stockStore.totalCount) return '0M'
  
  let maxVolume = 0
  
  Object.values(stockStore.processedData).forEach(data => {
    data.forEach(item => {
      // 先检查item.vol是否存在
      if (item && item.vol) {
        const volume = parseFloat(convertVolumeToNumber(item.vol))
        if (volume > maxVolume) {
          maxVolume = volume
        }
      }
    })
  })
  
  // 根据数值大小自动选择合适的单位显示
  if (maxVolume >= 1000000) {
    return `${(maxVolume / 1000000).toFixed(2)}M`
  } else if (maxVolume >= 1000) {
    return `${(maxVolume / 1000).toFixed(2)}K`
  } else {
    return `${maxVolume.toFixed(2)}`
  }
})

// 初始化图表
const initCharts = async () => {
  // 确保DOM已渲染
  await nextTick()
  
  // 获取DOM元素
  const priceChartEl = document.getElementById('priceTrendChart')
  const volumeChartEl = document.getElementById('volumeChangeChart')
  
  // 检查元素是否存在
  if (priceChartEl && !priceChart) {
    // 如果已有实例，先销毁
    if (priceChart) priceChart.dispose()
    priceChart = echarts.init(priceChartEl)
    priceChart.setOption({ 
      animation: false,
      title: {
        text: '请选择股票',
        left: 'center',
        top: 'middle',
        textStyle: { color: '#999' }
      }
    })
  }
  
  if (volumeChartEl && !volumeChart) {
    // 如果已有实例，先销毁
    if (volumeChart) volumeChart.dispose()
    volumeChart = echarts.init(volumeChartEl)
    volumeChart.setOption({ 
      animation: false,
      title: {
        text: '请选择股票',
        left: 'center',
        top: 'middle',
        textStyle: { color: '#999' }
      }
    })
  }

  window.addEventListener('resize', handleResize)
}

// 页面加载
onMounted(async () => {
  // 加载本地存储数据
  const savedData = localStorage.getItem('stockData')
  if (savedData) {
    try {
      const { processedData, stockNames: names, totalCount } = JSON.parse(savedData)
      stockStore.setProcessedData(processedData)
      stockStore.setStockNames(names)
      stockStore.setTotalCount(totalCount)
    } catch (e) {
      console.error('加载本地存储数据失败:', e)
      localStorage.removeItem('stockData')
    }
  }

  // 无数据时尝试加载默认CSV
  if (!stockStore.rawData.length) {
    try {
      const res = await fetch('/stock.csv', { cache: 'no-cache' })
      if (res.ok) {
        const csvText = await res.text()
        const file = new File([new Blob([csvText])], 'stock.csv')
        await stockStore.loadStockData(file)
      }
    } catch (err) {
      console.error('自动加载默认数据失败:', err)
      stockStore.error = '自动加载数据失败，请手动上传CSV文件'
    }
  }

  // 初始化图表 - 确保在DOM渲染后执行
  await initCharts()
  
  // 监听DOM元素是否存在后再更新图表
  const checkElementsAndUpdate = () => {
    if (document.getElementById('priceTrendChart') && 
        document.getElementById('volumeChangeChart') && 
        stockNames.value.length) {
      updatePriceChart()
      updateVolumeChart()
    } else {
      setTimeout(checkElementsAndUpdate, 100)
    }
  }
  
  checkElementsAndUpdate()
})

// 清理工作
onUnmounted(() => {
  window.removeEventListener('resize', handleResize)
  if (priceChart) {
    priceChart.dispose()
    priceChart = null
  }
  if (volumeChart) {
    volumeChart.dispose()
    volumeChart = null
  }
})

// 监听数据变化
watch(
  () => [stockStore.stockNames, stockStore.processedData],
  ([newNames, newData]) => {
    if (!newNames.length) return
    
    stockNames.value = newNames
    
    // 设置默认选中项 - 修复多选下拉框默认值问题
    if (!selectedStocks.value.length || selectedStocks.value.every(name => !newNames.includes(name))) {
      selectedStocks.value = newNames.slice(0, 2)
    } else {
      selectedStocks.value = selectedStocks.value.filter(name => newNames.includes(name))
      // 如果过滤后没有选中项，设置默认值
      if (!selectedStocks.value.length) {
        selectedStocks.value = [newNames[0]]
      }
    }

    if (!newNames.includes(selectedStockForVolume.value) || !selectedStockForVolume.value) {
      selectedStockForVolume.value = newNames[0] || ''
    }

    // 保存数据到本地
    localStorage.setItem('stockData', JSON.stringify({
      processedData: newData,
      stockNames: newNames,
      totalCount: stockStore.totalCount
    }))

    // 确保图表已初始化
    nextTick(() => {
      updatePriceChart()
      updateVolumeChart()
    })
  },
  { immediate: true, deep: true }
)

// 窗口大小调整
const handleResize = () => {
  if (priceChart) priceChart.resize()
  if (volumeChart) volumeChart.resize()
}

// 更新股价趋势图
const updatePriceChart = async () => {
  // 检查图表实例和DOM元素是否存在
  if (!priceChart || !document.getElementById('priceTrendChart') || !selectedStocks.value.length) {
    return
  }
  
  isPriceChartLoading.value = true
  priceChartNoData.value = false
  
  try {
    const series = await Promise.all(
      selectedStocks.value.map(stockName => {
        const stockData = stockStore.processedData[stockName] || []
        if (!stockData.length) return null
        
        let data = [...stockData]
          .sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp))
          .filter(item => filterDataByTimeRange(item, priceTimeRange.value))
          .map(item => [
            new Date(item.timestamp).getTime(), 
            parseFloat(item.last || 0),
            parseFloat(item.high || 0),
            parseFloat(item.low || 0)
          ])
        
        // 检查是否有数据
        if (data.length === 0) return null
        
        if (data.length > 5000) data = sampleData(data, 5000)

        return {
          name: stockName,
          type: 'line',
          data: data.map(item => [item[0], item[1]]),
          smooth: true,
          lineStyle: { width: 2, color: getStockColor(stockName) },
          symbol: 'none',
          emphasis: { symbol: 'circle', symbolSize: 6 },
          markLine: { 
            data: [{ type: 'average', name: '平均股价' }],
            label: { formatter: '{c:.2f}' }
          },
          // 添加最高最低价区间
          markArea: {
            silent: true,
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0, color: getStockColor(stockName) + '33' // 透明度33%
                }, {
                  offset: 1, color: getStockColor(stockName) + '00' // 透明度0%
                }]
              }
            },
            data: [[{
              type: 'min',
              valueDim: 'low',
              name: '最低价'
            }, {
              type: 'max',
              valueDim: 'high',
              name: '最高价'
            }]]
          }
        }
      })
    ).filter(Boolean)

    // 检查是否有有效系列数据
    if (series.length === 0) {
      priceChartNoData.value = true
      priceChart.setOption({
        title: {
          text: '所选时间范围内没有数据',
          left: 'center',
          top: 'middle',
          textStyle: { color: '#999' }
        },
        series: []
      })
      return
    }

    priceChart.setOption({
      title: { show: false }, // 有数据时隐藏标题
      tooltip: {
        trigger: 'axis',
        formatter: params => {
          const d = new Date(params[0].name)
          // 查找该时间点的最高价和最低价
          const pointData = series.find(s => s.name === params[0].seriesName)?.data
            .find(p => p[0] === params[0].name)
          
          return `<div><strong>${params[0].seriesName}</strong></div>
                  <div>${formatDate(d, true)}</div>
                  <div>最新价：$${params[0].value[1].toFixed(2)}</div>
                  <div>最高价：$${pointData?.[2].toFixed(2) || '-'}</div>
                  <div>最低价：$${pointData?.[3].toFixed(2) || '-'}</div>`
        }
      },
      legend: { data: selectedStocks.value, top: 0, icon: 'circle' },
      grid: { left: '3%', right: '4%', bottom: '3%', top: '15%', containLabel: true },
      xAxis: { type: 'time', axisLabel: { rotate: 30 } },
      yAxis: { 
        type: 'value', 
        name: '股价（USD）', 
        axisLabel: { formatter: '${value}' },
        min: function(value) {
          // 确保Y轴从低于最小值的位置开始，增强可视化效果
          return Math.floor(value.min * 0.95);
        }
      },
      series
    })
  } catch (err) {
    console.error('股价图表更新失败:', err)
    stockStore.error = '股价图表更新失败，请重试'
  } finally {
    isPriceChartLoading.value = false
  }
}

// 更新成交量分析图
const updateVolumeChart = async () => {
  // 检查图表实例和DOM元素是否存在
  if (!volumeChart || !document.getElementById('volumeChangeChart') || !selectedStockForVolume.value) return
  
  isVolumeChartLoading.value = true
  volumeChartNoData.value = false
  
  try {
    const stockData = stockStore.processedData[selectedStockForVolume.value] || []
    if (!stockData.length) {
      volumeChartNoData.value = true
      return
    }
    
    let data = [...stockData]
      .sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp))
      .filter(item => filterDataByTimeRange(item, volumeTimeRange.value))
      .map(item => ({
        time: new Date(item.timestamp).getTime(),
        // 修复：使用通用方法转换成交量
        volume: convertVolumeToNumber(item.vol),
        change: parseFloat(item.chgPercent?.replace(/%/g, '') || 0), // 修复：处理可能的undefined
        last: parseFloat(item.last || 0)
      }))
    
    // 检查是否有数据
    if (data.length === 0) {
      volumeChartNoData.value = true
      volumeChart.setOption({
        title: {
          text: '所选时间范围内没有数据',
          left: 'center',
          top: 'middle',
          textStyle: { color: '#999' }
        },
        series: []
      })
      return
    }
    
    if (data.length > 5000) data = sampleData(data, 5000, true)

    const volumeData = data.map(item => [item.time, item.volume])
    const changeData = data.map(item => [item.time, item.change])
    const color = getStockColor(selectedStockForVolume.value)

    volumeChart.setOption({
      title: { show: false }, // 有数据时隐藏标题
      tooltip: {
        trigger: 'axis',
        formatter: params => {
          const d = new Date(params[0].name)
          return `<div><strong>${selectedStockForVolume.value}</strong></div>
                  <div>${formatDate(d, true)}</div>
                  <div>最新价：$${params[2]?.value[1].toFixed(2) || '-'}</div>
                  <div>成交量：${formatVolume(params[0].value[1])}</div>
                  <div>涨跌幅：${params[1].value[1].toFixed(2)}%</div>`
        }
      },
      grid: [
        { left: '3%', right: '4%', bottom: '50%', top: '3%', containLabel: true },
        { left: '3%', right: '4%', bottom: '3%', top: '55%', containLabel: true }
      ],
      xAxis: [
        { type: 'time', axisLabel: { rotate: 30 } },
        { type: 'time', gridIndex: 1 }
      ],
      yAxis: [
        { type: 'value', name: '成交量', axisLabel: { 
          formatter: function(value) {
            return formatVolume(value, true);
          }
        } },
        { 
          type: 'value', 
          gridIndex: 1, 
          name: '涨跌幅（%）', 
          axisLabel: { formatter: '{value}%' },
          axisLine: {
            lineStyle: {
              color: '#666'
            }
          },
          splitLine: { show: false }
        }
      ],
      series: [
        {
          name: '成交量',
          type: 'bar',
          data: volumeData,
          barWidth: '30%',
          itemStyle: {
            color: ({data}) => {
              const change = changeData.find(c => c[0] === data[0])?.[1] || 0
              return change >= 0 ? '#28a745' : '#dc3545'
            }
          }
        },
        {
          name: '涨跌幅',
          type: 'line',
          xAxisIndex: 1,
          yAxisIndex: 1,
          data: changeData,
          lineStyle: { color },
          itemStyle: { color: ({data}) => data[1] >= 0 ? '#28a745' : '#dc3545' },
          markLine: { data: [{ yAxis: 0, name: '基准线' }] }
        },
        {
          name: '最新价',
          type: 'line',
          data: data.map(item => [item.time, item.last]),
          lineStyle: { color: '#17a2b8', type: 'dashed' },
          symbol: 'none',
          tooltip: { show: false }
        }
      ]
    })
  } catch (err) {
    console.error('成交量图表更新失败:', err)
    stockStore.error = '成交量图表更新失败，请重试'
  } finally {
    isVolumeChartLoading.value = false
  }
}

// 工具函数：数据采样
const sampleData = (data, count, isObject = false) => {
  if (data.length <= count) return data
  const step = Math.ceil(data.length / count)
  return data.filter((_, i) => i % step === 0).map(item => isObject ? {...item} : [...item])
}

// 工具函数：时间范围过滤
const filterDataByTimeRange = (item, range) => {
  if (range === 'all') return true
  
  const now = new Date()
  const cutoff = new Date()
  
  switch (range) {
    case '1d': cutoff.setDate(now.getDate() - 1); break
    case '1w': cutoff.setDate(now.getDate() - 7); break
    case '1m': cutoff.setMonth(now.getMonth() - 1); break
    case '3m': cutoff.setMonth(now.getMonth() - 3); break
    case '1y': cutoff.setFullYear(now.getFullYear() - 1); break
    default: return true
  }
  
  // 处理无效日期
  const itemDate = new Date(item.timestamp)
  return !isNaN(itemDate.getTime()) && itemDate >= cutoff
}

// 工具函数：股票颜色分配
const stockColors = {}
const defaultColors = ['#1f77b4', '#ff7f0e', '#2ca02c', '#d62728', '#9467bd', '#8c564b', '#e377c2', '#7f7f7f', '#bcbd22', '#17becf']

const getStockColor = (stockName) => {
  if (!stockName) return defaultColors[0]
  
  if (!stockColors[stockName]) {
    stockColors[stockName] = defaultColors[Object.keys(stockColors).length % defaultColors.length]
  }
  return stockColors[stockName]
}

// 工具函数：日期格式化
const formatDate = (date, withTime = false) => {
  // 检查日期有效性
  if (isNaN(date.getTime())) return '无效日期'
  
  const fmt = `${date.getFullYear()}-${(date.getMonth()+1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')}`
  return withTime 
    ? `${fmt} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
    : fmt
}

// 工具函数：成交量转换 - 修复核心
const convertVolumeToNumber = (volumeStr) => {
  // 首先检查volumeStr是否存在且为字符串类型
  if (!volumeStr || typeof volumeStr !== 'string') {
    console.warn('无效的成交量数据:', volumeStr)
    return 0
  }
  
  // 提取数字部分和单位，增强正则表达式以处理可能的空格
  const match = volumeStr.trim().match(/^(\d+(\.\d+)?)([MK]?)$/i)
  if (!match) {
    console.warn('无法解析的成交量格式:', volumeStr)
    return 0
  }
  
  const num = parseFloat(match[1])
  const unit = match[3]
  
  // 根据单位转换为实际数值
  switch (unit.toUpperCase()) {
    case 'M': // 百万
      return num * 1000000
    case 'K': // 千
      return num * 1000
    default: // 无单位，直接返回
      return num
  }
}

// 工具函数：格式化成交量显示
const formatVolume = (volumeNum, shortFormat = false) => {
  if (volumeNum >= 1000000) {
    return shortFormat ? `${(volumeNum / 1000000).toFixed(1)}M` : `${(volumeNum / 1000000).toFixed(2)}M`
  } else if (volumeNum >= 1000) {
    return shortFormat ? `${(volumeNum / 1000).toFixed(1)}K` : `${(volumeNum / 1000).toFixed(2)}K`
  } else {
    return volumeNum.toFixed(0)
  }
}

// 文件上传处理
const handleFileUpload = async (e) => {
  const file = e.target.files[0]
  if (!file) return
  
  if (!file.name.endsWith('.csv')) {
    stockStore.error = '请上传CSV格式的文件'
    return
  }
  
  try {
    stockStore.isLoading = true
    await stockStore.loadStockData(file)
    e.target.value = ''
  } catch (err) {
    console.error('文件解析失败:', err)
    stockStore.error = '文件解析失败，请检查文件格式是否符合要求'
  } finally {
    stockStore.isLoading = false
  }
}

// 清除错误
const clearError = () => stockStore.error = ''
</script>

