<template>
  <div class="data-list-container">
    <!-- 顶部操作栏 -->
    <div class="top-bar">
      <h2 class="title">股票数据列表</h2>
      <div class="search-sort">
        <input
          v-model="searchQuery"
          type="text"
          placeholder="搜索股票名称..."
          class="search-input"
        />
        <select v-model="sortField" class="sort-select">
          <option value="name">按名称排序</option>
          <option value="last">按最新价格排序</option>
          <option value="chgPercent">按涨跌幅排序</option>
        </select>
        <button @click="sortData" class="sort-btn">
          <i class="fas fa-sort"></i> 排序
        </button>
      </div>
    </div>

    <!-- 数据表格 -->
    <div class="data-table-wrapper">
      <table class="data-table">
        <thead>
          <tr>
            <th>时间戳</th>
            <th>股票名称</th>
            <th>最新价格</th>
            <th>最高价</th>
            <th>最低价</th>
            <th>涨跌额</th>
            <th>涨跌幅</th>
            <th>成交量</th>
            <th>更新时间</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in filteredData"
            :key="index"
            :class="{ even: index % 2 === 0 }"
          >
            <td>{{ item.timestamp }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.last }}</td>
            <td>{{ item.high }}</td>
            <td>{{ item.low }}</td>
            <td :class="item.chg_ >= 0 ? 'positive' : 'negative'">
              {{ item.chg_ }}
            </td>
            <td :class="item.chgPercent >= 0 ? 'positive' : 'negative'">
              {{ item.chgPercent }}%
            </td>
            <td>{{ item.vol_ }}</td>
            <td>{{ item.time }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- 无数据提示 -->
    <div v-if="filteredData.length === 0" class="no-data">
      没有找到匹配的数据
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchQuery: '',
      sortField: 'name',
      sortAscending: true,
      // 示例数据
      dataList: [
        {
          timestamp: '2025/6/11 7:46',
          name: '3M',
          last: 145.06,
          high: 145.31,
          low: 143.15,
          chg_: 0.83,
          chgPercent: 0.58,
          vol_: '3.13M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Amazon.com',
          last: 217.61,
          high: 217.69,
          low: 214.15,
          chg_: 0.63,
          chgPercent: 0.29,
          vol_: '31.18M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'American Express',
          last: 299.52,
          high: 301.78,
          low: 298,
          chg_: -2.04,
          chgPercent: -0.68,
          vol_: '2.05M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Amgen',
          last: 293.43,
          high: 295.64,
          low: 289.79,
          chg_: 3.45,
          chgPercent: 1.19,
          vol_: '2.44M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Apple',
          last: 202.67,
          high: 204.35,
          low: 200.57,
          chg_: 1.22,
          chgPercent: 0.61,
          vol_: '52M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Boeing',
          last: 215.73,
          high: 217.9,
          low: 213.25,
          chg_: -1.78,
          chgPercent: -0.82,
          vol_: '8.19M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Caterpillar',
          last: 358.57,
          high: 360.36,
          low: 356.09,
          chg_: 0.5,
          chgPercent: 0.14,
          vol_: '1.89M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Chevron',
          last: 143.35,
          high: 144.94,
          low: 141.91,
          chg_: 2.59,
          chgPercent: 1.84,
          vol_: '9.84M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Cisco',
          last: 65.15,
          high: 66.08,
          low: 64.81,
          chg_: -0.75,
          chgPercent: -1.14,
          vol_: '16.66M',
          time: '10月6日',
        },
        {
          timestamp: '2025/6/11 7:46',
          name: 'Coca-Cola',
          last: 72.35,
          high: 72.69,
          low: 71.76,
          chg_: 0.58,
          chgPercent: 0.81,
          vol_: '12.26M',
          time: '10月6日',
        },
      ],
    };
  },
  computed: {
    filteredData() {
      const query = this.searchQuery.toLowerCase();
      return this.dataList.filter((item) =>
        item.name.toLowerCase().includes(query)
      );
    },
  },
  methods: {
    sortData() {
      this.dataList.sort((a, b) => {
        let valueA = a[this.sortField];
        let valueB = b[this.sortField];

        // 处理数值比较
        if (typeof valueA === 'number' && typeof valueB === 'number') {
          return this.sortAscending
            ? valueA - valueB
            : valueB - valueA;
        }

        // 处理字符串比较
        valueA = String(valueA).toLowerCase();
        valueB = String(valueB).toLowerCase();
        return this.sortAscending
          ? valueA.localeCompare(valueB)
          : valueB.localeCompare(valueA);
      });

      // 切换排序方向
      this.sortAscending = !this.sortAscending;
    },
  },
};
</script>

<style scoped>
.data-list-container {
  font-family: Arial, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.search-sort {
  display: flex;
  gap: 10px;
}

.search-input {
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

.sort-select {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

.sort-btn {
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.sort-btn:hover {
  background-color: #0056b3;
}

.data-table-wrapper {
  overflow-x: auto;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
}

.data-table th,
.data-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.data-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.data-table tr:hover {
  background-color: #f5f5f5;
}

.data-table tr.even {
  background-color: #f9f9f9;
}

.positive {
  color: green;
}

.negative {
  color: red;
}

.no-data {
  text-align: center;
  padding: 20px;
  color: #666;
}
</style>