<template>
  <div class="stock-analysis-container">
    <div class="page-header">
      <h1>个股分析</h1>
      <p>详细分析单只股票的历史表现和关键指标</p>
    </div>
    
    <!-- 股票选择器 -->
    <div class="stock-selector">
      <label for="stock-select">选择股票:</label>
      <select id="stock-select" v-model="selectedStock" @change="loadStockData">
        <option v-for="stock in availableStocks" :key="stock.symbol" :value="stock.symbol">
          {{ stock.name }} ({{ stock.symbol }})
        </option>
      </select>
    </div>
    
    <!-- 股票基本信息 -->
    <div class="stock-info-card" v-if="currentStockData">
      <div class="info-item">
        <span class="info-label">当前价格</span>
        <span class="info-value">{{ formatNumber(currentStockData.price) }}</span>
      </div>
      <div class="info-item">
        <span class="info-label">今日涨跌幅</span>
        <span class="info-value" :class="currentStockData.change >= 0 ? 'positive' : 'negative'">
          {{ formatNumber(currentStockData.change) }} ({{ formatNumber(currentStockData.changePercent) }}%)
        </span>
      </div>
      <div class="info-item">
        <span class="info-label">今日开盘</span>
        <span class="info-value">{{ formatNumber(currentStockData.open) }}</span>
      </div>
      <div class="info-item">
        <span class="info-label">今日最高</span>
        <span class="info-value">{{ formatNumber(currentStockData.high) }}</span>
      </div>
      <div class="info-item">
        <span class="info-label">今日最低</span>
        <span class="info-value">{{ formatNumber(currentStockData.low) }}</span>
      </div>
      <div class="info-item">
        <span class="info-label">成交量</span>
        <span class="info-value">{{ currentStockData.volume || 'N/A' }}</span>
      </div>
    </div>
    
    <!-- 股票关键指标 -->
    <div class="metrics-container" v-if="currentStockData && currentStockData.metrics">
      <h2>关键指标</h2>
      <div class="metrics-grid">
        <div class="metric-card">
          <h3>市盈率 (P/E)</h3>
          <p class="metric-value">{{ formatNumber(currentStockData.metrics.peRatio) }}</p>
        </div>
        <div class="metric-card">
          <h3>市净率 (P/B)</h3>
          <p class="metric-value">{{ formatNumber(currentStockData.metrics.pbRatio) }}</p>
        </div>
        <div class="metric-card">
          <h3>股息率</h3>
          <p class="metric-value">{{ formatNumber(currentStockData.metrics.dividendYield) }}%</p>
        </div>
        <div class="metric-card">
          <h3>市值</h3>
          <p class="metric-value">{{ currentStockData.metrics.marketCap || 'N/A' }}</p>
        </div>
        <div class="metric-card">
          <h3>52周最高</h3>
          <p class="metric-value">{{ formatNumber(currentStockData.metrics.week52High) }}</p>
        </div>
        <div class="metric-card">
          <h3>52周最低</h3>
          <p class="metric-value">{{ formatNumber(currentStockData.metrics.week52Low) }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, onMounted } from 'vue'

// 可用股票列表
const availableStocks = ref([
  { symbol: 'AAPL', name: '苹果公司' },
  { symbol: 'MSFT', name: '微软公司' },
  { symbol: 'AMZN', name: '亚马逊' },
  { symbol: 'GOOGL', name: '谷歌' },
  { symbol: 'META', name: '元宇宙' }
])

// 选中的股票
const selectedStock = ref('AAPL')

// 当前股票数据
const currentStockData = ref(null)

// 格式化数字，避免null/undefined错误
const formatNumber = (num) => {
  if (num === null || num === undefined) return 'N/A'
  return num.toFixed(2)
}

// 加载股票数据
const loadStockData = () => {
  // 随机生成当前价格
  const basePrice = {
    'AAPL': 180,
    'MSFT': 320,
    'AMZN': 130,
    'GOOGL': 125,
    'META': 300
  }[selectedStock.value] || 150
  
  const price = basePrice + (Math.random() * 20 - 10)
  const change = (Math.random() * 5 - 2)
  
  currentStockData.value = {
    price,
    change,
    changePercent: (change / price) * 100,
    open: price - (Math.random() * 3),
    high: price + (Math.random() * 2),
    low: price - (Math.random() * 4),
    volume: (Math.floor(5000000 + Math.random() * 15000000) / 1000000).toFixed(2) + 'M',
    metrics: {
      peRatio: 15 + Math.random() * 20,
      pbRatio: 2 + Math.random() * 8,
      dividendYield: Math.random() * 5,
      marketCap: (1 + Math.random() * 2).toFixed(1) + '万亿',
      week52High: price + (Math.random() * 50),
      week52Low: price - (Math.random() * 50)
    }
  }
}

// 监听选中股票变化
watch(selectedStock, loadStockData)

// 页面加载完成后加载数据
onMounted(loadStockData)
</script>

<style scoped>
.stock-analysis-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  text-align: center;
  margin-bottom: 20px;
}

.stock-selector {
  margin-bottom: 20px;
}

.stock-info-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
}

.info-item {
  width: 100%;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .info-item { width: 33%; }
}

.info-label {
  font-weight: bold;
  margin-right: 10px;
}

.info-value {
  font-size: 1.2em;
  font-weight: bold;
}

.positive { color: #2563eb; }
.negative { color: #eb2525; }

.metrics-container { margin-bottom: 20px; }
.metrics-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

@media (min-width: 768px) {
  .metrics-grid { grid-template-columns: repeat(3, 1fr); }
}

.metric-card {
  background-color: #f5f5f5;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
}

.metric-card h3 { margin-bottom: 5px; }
.metric-card p.metric-value {
  font-size: 1.2em;
  font-weight: bold;
}
</style>
