<template>
  <aside class="sidebar">
    <!-- 侧边栏标题 -->
    <div class="sidebar-header">
      <h3 class="sidebar-title">股票分析系统</h3>
    </div>
    
    <!-- 导航菜单 -->
    <nav class="sidebar-menu">
      <router-link 
        to="/" 
        class="menu-item"
        :class="{ active: $route.path === '/' }"
      >
        <i class="bi bi-grid-3x3-gap"></i>
        <span>市场概况</span>
      </router-link>
      
      <router-link 
        to="/stock-analysis" 
        class="menu-item"
        :class="{ active: $route.path === '/stock-analysis' }"
      >
        <i class="bi bi-bar-chart-line"></i>
        <span>个股分析</span>
      </router-link>
      
      <router-link 
        to="/data-list" 
        class="menu-item"
        :class="{ active: $route.path === '/data-list' }"
      >
        <i class="bi bi-table"></i>
        <span>数据列表</span>
      </router-link>
    </nav>
    
    <!-- 股票分类筛选 -->
    <div class="sidebar-filter mt-4">
      <h4 class="section-title">股票分类</h4>
      <div class="filter-tags">
        <button 
          v-for="category in stockCategories"
          :key="category.value"
          class="tag-btn"
          :class="{ active: selectedCategory === category.value }"
          @click="handleCategoryChange(category.value)"
        >
          {{ category.label }}
        </button>
      </div>
    </div>
    
    <!-- 涨幅排行 -->
    <div class="top-gainers mt-4">
      <h4 class="section-title">涨幅排行</h4>
      <div class="gainer-list">
        <div class="gainer-item" v-for="(stock, index) in topGainers" :key="index">
          <span class="gainer-name">{{ stock.name }}</span>
          <span class="gainer-change positive">{{ stock.chgPercent }}%</span>
        </div>
      </div>
    </div>
    
    <!-- 交易量排行 -->
    <div class="top-volume mt-4">
      <h4 class="section-title">交易量排行</h4>
      <div class="volume-list">
        <div class="volume-item" v-for="(stock, index) in topVolume" :key="index">
          <span class="volume-name">{{ stock.name }}</span>
          <span class="volume-value">{{ stock.vol }}</span>
        </div>
      </div>
    </div>
    
    <!-- 数据更新时间 -->
    <div class="sidebar-footer" v-if="lastUpdateTime">
      <p>最后更新：{{ lastUpdateTime }}</p>
    </div>
  </aside>
</template>

<script setup>
import { ref, computed } from 'vue'
import { useStockStore } from '@/stores/stockStore.js'

const stockStore = useStockStore()
const lastUpdateTime = computed(() => stockStore.lastUpdateTime || '2023-10-06')

// 股票分类
const stockCategories = ref([
  { label: '全部', value: 'all' },
  { label: '科技股', value: 'tech' },
  { label: '工业股', value: 'industrial' },
  { label: '消费股', value: 'consume' },
  { label: '医药股', value: 'pharmaceutical' }
])
const selectedCategory = ref('all')

// 处理分类变更
const handleCategoryChange = (value) => {
  selectedCategory.value = value
  if (stockStore.setSelectedCategory) {
    stockStore.setSelectedCategory(value)
  }
}

// 股票数据处理
const stockData = ref([
  { name: '3M', last: 145.06, high: 145.31, low: 143.15, chg: 0.83, chgPercent: 0.58, vol: '3.13M' },
  { name: 'Amazon', last: 217.61, high: 217.69, low: 214.15, chg: 0.63, chgPercent: 0.29, vol: '31.18M' },
  { name: 'American', last: 299.52, high: 301.78, low: 298, chg: -2.04, chgPercent: -0.68, vol: '2.05M' },
  { name: 'Amgen', last: 293.43, high: 295.64, low: 289.79, chg: 3.45, chgPercent: 1.19, vol: '2.44M' },
  { name: 'Apple', last: 202.67, high: 204.35, low: 200.57, chg: 1.22, chgPercent: 0.61, vol: '52M' },
  { name: 'Boeing', last: 215.73, high: 217.9, low: 213.25, chg: -1.78, chgPercent: -0.82, vol: '8.19M' },
  { name: 'Caterpillar', last: 358.57, high: 360.36, low: 356.09, chg: 0.5, chgPercent: 0.14, vol: '1.89M' },
  { name: 'Chevron', last: 143.35, high: 144.94, low: 141.91, chg: 2.59, chgPercent: 1.84, vol: '9.84M' },
  { name: 'Cisco', last: 65.15, high: 66.08, low: 64.81, chg: -0.75, chgPercent: -1.14, vol: '16.66M' },
  { name: 'Coca-Cola', last: 72.35, high: 72.69, low: 71.76, chg: 0.58, chgPercent: 0.81, vol: '12.26M' }
])

// 获取涨幅前3的股票
const topGainers = computed(() => {
  return [...stockData.value]
    .sort((a, b) => b.chgPercent - a.chgPercent)
    .slice(0, 3)
})

// 获取成交量前3的股票
const topVolume = computed(() => {
  return [...stockData.value]
    .sort((a, b) => {
      const volA = parseFloat(a.vol) * (a.vol.includes('M') ? 1 : 0.001)
      const volB = parseFloat(b.vol) * (b.vol.includes('M') ? 1 : 0.001)
      return volB - volA
    })
    .slice(0, 3)
})
</script>

<style scoped>
.sidebar {
  width: 260px;
  background-color: #fff;
  border-right: 1px solid #e5e7eb;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.sidebar-header {
  padding: 0 20px 15px;
  border-bottom: 1px solid #f0f0f0;
}

.sidebar-title {
  font-size: 18px;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.sidebar-menu {
  padding: 15px 0;
}

.menu-item {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  color: #4b5563;
  text-decoration: none;
  transition: all 0.2s;
  gap: 10px;
}

.menu-item:hover {
  background-color: #f9fafb;
  color: #2563eb;
}

.menu-item.active {
  background-color: #eff6ff;
  color: #2563eb;
  border-right: 3px solid #2563eb;
}

.section-title {
  font-size: 14px;
  color: #6b7280;
  margin: 0 0 10px 0;
  font-weight: 500;
  padding: 0 20px;
}

.top-gainers, .top-volume {
  padding: 15px 0;
  border-top: 1px solid #f0f0f0;
}

.sidebar-filter {
  padding: 15px 0;
  border-top: 1px solid #f0f0f0;
}

.filter-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 0 20px;
}

.tag-btn {
  padding: 4px 10px;
  border-radius: 16px;
  border: 1px solid #e5e7eb;
  background-color: #fff;
  color: #6b7280;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s;
}

.tag-btn:hover {
  border-color: #2563eb;
  color: #2563eb;
}

.tag-btn.active {
  background-color: #2563eb;
  color: #fff;
  border-color: #2563eb;
}

.gainer-list, .volume-list {
  padding: 0 20px;
}

.gainer-item, .volume-item {
  display: flex;
  justify-content: space-between;
  padding: 6px 0;
  font-size: 13px;
  border-bottom: 1px solid #f9fafb;
}

.gainer-item:last-child, .volume-item:last-child {
  border-bottom: none;
}

.gainer-name, .volume-name {
  color: #4b5563;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 140px;
}

.gainer-change {
  font-weight: 500;
}

.volume-value {
  color: #6b7280;
  font-weight: 500;
}

.sidebar-footer {
  padding: 15px 20px;
  border-top: 1px solid #f0f0f0;
  font-size: 12px;
  color: #9ca3af;
  margin-top: auto;
}

.sidebar-footer p {
  margin: 0;
}

.positive {
  color: #10b981;
}

.negative {
  color: #ef4444;
}
</style>
